// 物流消息
import ali from '@/components/WuLiu-step.vue';
export default {
	components: {
		ali
	},
	onLoad(e) {
		this.logistics(e.order_id);
	},
	data() {
		return {
			courierInfo:{
				delivery_name:"快递",
				delivery_id:"正在加载...",
				user_address:"正在加载..."
			},
			result:{
				deliverystatus:0,
				expPhone:"",
				list:[],
				expName: '',
			},
		};
	},
	methods: {
		logistics(order_id) {
			let that = this;
			that.baseGet(
				that.U({
					c: 'user_api',
					a: 'express',
					q: {
						uni: order_id
					}
				}),
				function(res) {
					that.courierInfo=res.data.order;
					that.result=res.data.express.result;
					console.log(that.result);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		}
	}
};
